import React, { Fragment } from 'react';

import Logo from '../../assets/gopher.jpeg';
import ImageView from './ImageView';
import Wrapper from './Wrapper';
import Title from './Title';
import Description from './Description';
import Divider from './ Divider';

const About = ({title, adverts}) => {
    return (
        <Wrapper>
            <ImageView imgURL={Logo} />
            <Title title={title.title} subtitle={title.subtitle}/>
            {adverts.map(({title, subtitle, content, hasSubTitleLink, link}, idx) => {
                if(idx < adverts.length -1 ) {
                    return (
                        <Fragment>
                            <Description 
                                title={title} 
                                subtitle={subtitle} 
                                content={content} 
                                hasSubTitleLink={hasSubTitleLink} 
                                link={link} 
                            />
                            <Divider />
                        </Fragment>
                    );
                }
                return <Description 
                            title={title} 
                            subtitle={subtitle} 
                            content={content} 
                            hasSubTitleLink={hasSubTitleLink} 
                            link={link} 
                        />
            })}
        </Wrapper>
    );
};

export default About;