import React from 'react';
import PropTypes from 'prop-types';

import Wrapper from './Wrapper';
import LinkTO from './Link';

const Button = ({text, to}) => {
    return (
        <Wrapper>
            <LinkTO to={to}>
                {text}
            </LinkTO>
        </Wrapper>
    )
};

Button.propTypes = {
    text: PropTypes.string,
    to: PropTypes.string,
};
export default Button;