import React, { Fragment } from 'react';
import PropTypes from 'prop-types';

import Wrapper from './Wrapper';
import SubTitle from './Subtitle';
import Title from './Title';
import SubtitleLink from './SubtitleLink';

const Description = ({title, subtitle, content, hasSubTitleLink, link}) => {
    return (
        <Wrapper>
            {title ? <Title>{title}</Title>
            : null}
            {subtitle ? hasSubTitleLink ? <SubtitleLink href={link}>{subtitle}</SubtitleLink> 
            : <SubTitle>{subtitle}</SubTitle> 
            : null}
            {content ? <Fragment>{content}</Fragment> : null}
        </Wrapper>
    );
};

Description.propTypes = {
    title: PropTypes.string,
    subtitle: PropTypes.string,
    content: PropTypes.string,
};

export default Description;